

main {
  flex-grow: 1;
 
  
}
footer {
  width:100%;
  height: min-content;
}

a {
  text-decoration: none;
  color:#000;
}
.active {
  color:#C20101;
  text-decoration: underline;
  text-underline-offset:3px;

}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
